
  import XProgress from 'vux/src/components/x-progress/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

  export default {
    components: {

      XProgress,
      Actionsheet,
      Confirm
    },
     directives: {
      TransferDom
    },
    data() {
      return {
        onEditingIndex :-1,
        baseUrl: M.baseImgUrl,
        imageSuffix: '?imageView2/1/w/260/h/180/q/80',
        userObj: {},
        screenWidth: 375,
        menuShowFlag: false,
        startedLessonArr: [],

        //菜单
        menus: {
          doEdit: '编辑',
          doDelete: '删除'
        },
        showMenus: false, //是否显示删除菜单
        selectListId: null,//需要删除的课程ID
        selectListIndex: null,//需要删除课程的位置
        selectListType: null,//需要删除课程的类型

        onShowComfirm: false,

      }
    },
    methods: {
      goEditPage(){
        var url = window.location.origin + '/admin/#/admin/punch?type=attendance';
        window.location.href = url;
      },

      onCancelDel () {

      },
      onConfirmDel () {

        var id = this.selectListId;
        var index = this.selectListIndex;
        var type = this.selectListType;
        var _self = this;
        this.H.toAjax(true).then(function (comData) { //data 公用参数
          comData.data = {activityId: id};
          _self.A.post("v1/attendance/delete", comData)
            .then(function (result) {
              if (result.data.state.code == 0) {
                _self.startedLessonArr.splice(index, 1);
              }
            }).catch(function (result) {
            console.log(result);
          });
        });
      },
      //点击开课
      showTheMenu(){
        this.$emit('showMenus');
      },
      //跳转
      goToPage(lessonId, lessonType){
        this.showLoading();
        this.$router.push({path: "/admin/punchShow/meet", query: {id: lessonId, type: lessonType}});
      },
      //点击倒三角
      showMenu(id, index, type){
        this.showMenus = true;
        this.selectListId = id;
        this.selectListIndex = index;
        this.selectListType = type;
      },
      //删除课程
      doDeleteList(val){
        var id = this.selectListId;
        var index = this.selectListIndex;
        var type = this.selectListType;
        var _self = this;
        if (val == 'doDelete') {
            this.onShowComfirm = true;

        }
        if (val == 'doEdit') {
          this.$router.push({path: '/admin/punchShow/newSubmit', query:{id: id, type: type}});
        }
//        _self.$emit('$showToolBar',true);
      }
    },
    watch:{
      "showMenus" (status){
        if(status){
          this.onEditingIndex = this.selectListIndex ;
          this.$emit("$showToolBar",false);
        }else{
          this.onEditingIndex = -1 ;
          this.$emit("$showToolBar",true);
        }
      }
    },
    beforeMount(){
      //如果没有用户登录则退出方法
      if(!M.F.D.get("user")) return ;

      var wechatShareData = {};
      var userCode = '';
      //微信签名信息
      var config = {
//          debug: true,
        appId: "wx58f3e6eb9f9a1938",
        timestamp : (parseInt((new Date().getTime())/1000)).toString(),
        nonceStr : M.F.getId(16),
      };
      if(M.isQC || M.isDev){
        config.appId = M.appId
      }

      var configData = {
        noncestr : config.nonceStr,
        timestamp: config.timestamp,
        url: window.location.href.split('#')[0]
      };

      var _self = this;

      _self.H.toAjax(true).then(function (data) {
        //先获取userCode,再获取分享信息,再设置configSign
        var submitData = data;
        _self.A.post("/v1/userbind/userCode",submitData).then(function (userCodeResult) {
          if(userCodeResult.data.state.code == 0) {
            userCode = userCodeResult.data.data.userCode;
            submitData.data = {
              userCode: userCode
            }
            return _self.A.post("/v1/userbind/shareQrAndNameAndhead",submitData);
          } else {
//            _self.$emit("modalAlert",userCodeResult.data.state.msg,"确定");
          }
        }).then(function (wxAccountResult) {
          if(!wxAccountResult || !wxAccountResult.data){
            return
          }
          if(wxAccountResult.data.state.code == 0) {
            var wxAccountResultData = wxAccountResult.data.data;
            var appHeadImgUrl = '';
            var appName = '';
            var userName = '';

            //头像
            wxAccountResultData.appHeadImg &&
            wxAccountResultData.appHeadImg != null &&
            wxAccountResultData.appHeadImg.url &&
            wxAccountResultData.appHeadImg.url != "" &&
            wxAccountResultData.appHeadImg.url != null ?
              appHeadImgUrl = _self.baseUrl + wxAccountResultData.appHeadImg.url : appHeadImgUrl = 'http://image.artyears.cn/template/zhishiquan.jpg';
            //公众号名称
            wxAccountResultData.appName &&
            wxAccountResultData.appName != null
              ? appName = wxAccountResultData.appName : appName = '知识圈';
            //用户名
            wxAccountResultData.userName ? userName = wxAccountResultData.userName : userName = '';

            wechatShareData.title = appName;  //标题
            appName == '知识圈' ? wechatShareData.desc = userName + "向你推荐了几个有趣的活动" : wechatShareData.desc = userName + "向你推荐来自" + appName + "公众号的活动";
            wechatShareData.link = window.location.origin + "/user/index?userCode=" + userCode;
            wechatShareData.imgUrl = appHeadImgUrl;

            console.log("wechatShareData",wechatShareData);

            //获取签名
            submitData.data = configData;
            return _self.A.post("/v1/common/configSign",submitData);

          } else {
//            _self.$emit("modalAlert",wxAccountResult.data.state.msg,"确定");
          }
        }).then(function (configSignResult) {
          if(!configSignResult || !configSignResult.data) {
            return;
          }
          config.signature = configSignResult.data.data.sign;
          config.jsApiList = [
            'hideAllNonBaseMenuItem',
            'onMenuShareTimeline',
            'onMenuShareAppMessage'
          ];

          _self.$wechat.config({
//            debug: true,
            appId: config.appId,
            timestamp: config.timestamp,
            nonceStr: config.nonceStr,
            signature: config.signature,
            jsApiList: config.jsApiList
          });

          _self.$wechat.ready(function () {
            //分享到朋友圈
            _self.$wechat.onMenuShareTimeline({
              title: wechatShareData.title, // 分享标题
              link: wechatShareData.link, // 分享链接
              imgUrl: wechatShareData.imgUrl, // 分享图标
            });
            //分享给微信好友
            _self.$wechat.onMenuShareAppMessage({
              title: wechatShareData.title, // 分享标题
              desc: wechatShareData.desc, // 分享描述
              link: wechatShareData.link, // 分享链接
              imgUrl: wechatShareData.imgUrl, // 分享图标
              type: 'link', // 分享类型,music、video或link，不填默认为link
              dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
            });
          });

        });
      });

    },
    mounted() {
      document.getElementById("title").innerHTML = "首页";
      this.showLoading();
      var _self = this;
      _self.$emit("$showToolBar",true);
      //if(M.F.L.get("user"))return ;
      this.H.toAjax(true).then(function (data) { //data 公用参数
        var submitData = data;
        submitData.data = {
          type: "all",
          page: {
            page: 1,
            size: 50
          }
        };
        var isBindMobile = M.F.D.get("isBindMobile");
        if(isBindMobile){

          _self.A.post("/v1/attendance/list", submitData).then(function (result) {
            console.log(result);
            if (result.data.state.code == 0 ) {
              console.info("有反悔数据");
              _self.hideLoading();
             var activities =   result.data.data && result.data.data.activities.length ==0 ?[]: result.data.data.activities;
              _self.startedLessonArr = activities;
            }
          })
        }else{
              _self.A.post("/v1/auth/get",data).then(function(result) {

                if(!result.data.data.isBindMobile){
                  // 跳转到
                  M.toPath("/admin/login");
                }else{
                  M.user.isBindMobile = true ;
                  M.F.D.set("isBindMobile",true);
                  _self.A.post("/v1/attendance/list", submitData).then(function (result) {
                  console.log(result);
                  _self.hideLoading();
                  if (result.data.state.code== 0  ) {
                    console.info("有数据");
                    var activities =   result.data.data && result.data.data.activities.length ==0 ?[]: result.data.data.activities;
                    _self.startedLessonArr = activities;
                  }
                })
                }
              })

        }
      });
    }
  }



